@charset "UTF-8";

// ------------------------------------------------------------------------
// Fonts
//
// Variable axes:
// 'wdth' from 0 to 200
// 'wght' from 0 to 1000
// 'ital' from 0 to 12
// ------------------------------------------------------------------------

@font-face {
  font-family: "Antarctica";
  src: url("../fonts/Antarctica_VAR-VF.ttf.woff2") format("woff2");
  font-weight: 400; /* Default weight */
  font-style: normal;
}
$font-sans: "Antarctica", sans-serif;

@mixin font-style(
  $font-fam,
  $xs,
  $sm,
  $md,
  $lg,
  $xl,
  $letter-sp,
  $line-h,
  $wght,
  $wdth,
  $ital,
  $style
) {
  font-family: $font-fam;
  font-size: $xs;
  letter-spacing: $letter-sp;
  line-height: $line-h;
  // font-weight: $weight; base defaults to 400 as specified in import
  font-style: $style;
  font-stretch: normal;
  font-variation-settings: "wght" $wght, "wdth" $wdth, "ital" $ital; /* Controls weight and width */
  transition: font-variation-settings 400ms;
  @include media-breakpoint-up(sm) {
    font-size: $sm;
  }
  @include media-breakpoint-up(md) {
    font-size: $md;
  }
  @include media-breakpoint-up(lg) {
    font-size: $lg;
  }
  @include media-breakpoint-up(xl) {
    font-size: $xl;
  }
}

// ---------------------------------------------------- xs --- sm --- md --- lg --- xl ---- lsp ---- lh ---- w --- style

@mixin font-sans-xl($wght: 400, $wdth: 100, $ital: 0) {
  @include font-style(
    $font-fam: $font-sans,
    $xs: 52px,
    $sm: 52px,
    $md: 60px,
    $lg: 60px,
    $xl: 60px,
    $letter-sp: 0,
    $line-h: 1.2em,
    $wght: $wght,
    $wdth: $wdth,
    $ital: $ital,
    $style: normal
  );
}
@mixin font-sans-ll($wght: 400, $wdth: 100, $ital: 0) {
  @include font-style(
    $font-fam: $font-sans,
    $xs: 24px,
    $sm: 24px,
    $md: 27px,
    $lg: 27px,
    $xl: 27px,
    $letter-sp: 0,
    $line-h: 1.2em,
    $wght: $wght,
    $wdth: $wdth,
    $ital: $ital,
    $style: normal
  );
}
@mixin font-sans-l($wght: 400, $wdth: 100, $ital: 0) {
  @include font-style(
    $font-fam: $font-sans,
    $xs: 18px,
    $sm: 18px,
    $md: 21px,
    $lg: 21px,
    $xl: 21px,
    $letter-sp: 0,
    $line-h: 1.2em,
    $wght: $wght,
    $wdth: $wdth,
    $ital: $ital,
    $style: normal
  );
}
@mixin font-sans-m($wght: 400, $wdth: 100, $ital: 0) {
  @include font-style(
    $font-fam: $font-sans,
    $xs: 15px,
    $sm: 15px,
    $md: 16px,
    $lg: 16px,
    $xl: 16px,
    $letter-sp: 0,
    $line-h: 1.5em,
    $wght: $wght,
    $wdth: $wdth,
    $ital: $ital,
    $style: normal
  );
}
@mixin font-sans-s($wght: 400, $wdth: 100, $ital: 0) {
  @include font-style(
    $font-fam: $font-sans,
    $xs: 12px,
    $sm: 12px,
    $md: 13px,
    $lg: 13px,
    $xl: 13px,
    $letter-sp: 0.01em,
    $line-h: 1.3em,
    $wght: $wght,
    $wdth: $wdth,
    $ital: $ital,
    $style: normal
  );
}
@mixin font-sans-xs($wght: 400, $wdth: 100, $ital: 0) {
  @include font-style(
    $font-fam: $font-sans,
    $xs: 11px,
    $sm: 11px,
    $md: 11px,
    $lg: 11px,
    $xl: 11px,
    $letter-sp: 0.03em,
    $line-h: 1.3em,
    $wght: $wght,
    $wdth: $wdth,
    $ital: $ital,
    $style: normal
  );
}

