@charset "UTF-8";

// -----------------------------------------------------------------------------
// Variables
// -----------------------------------------------------------------------------

$menu-h: 55px;
$dataviz-header-h: 55px;
$border-radius: 5px;
$space: 20px;
$grid-max-width: 940px;
$grid-wide-max-width: 1200px;

$color-bg: #e9ecf5;
$color-bg-filters: #dbe3eb;

$color-luca-1: #a629ff;
$color-luca-2: #0041f7;

// -----------------------------------------------------------------------------
// External files import
// -----------------------------------------------------------------------------

@import "bootstrap-scss/_functions";
@import "bootstrap-scss/_variables";
@import "bootstrap-scss/mixins/_breakpoints";
@import "_reset";
@import "_typography";
@import "_mixins";
@import "_switch";
@import "hamburger-settings";
@import "hamburgers/hamburgers";

// -----------------------------------------------------------------------------
// Utilities
// -----------------------------------------------------------------------------

.color-blue {
  color: $color-luca-2 !important;
}
.color-white {
  color: white;
}
.color-white-50 {
  color: rgba(white, 0.5);
}
.bg-white {
  background-color: white;
}
.bg-color-bg {
  background-color: $color-bg;
}
.bg-color-bg-filters {
  background-color: $color-bg-filters;
}
.h-menu-h {
  height: $menu-h;
}
.no-u {
  text-decoration: none;
}
.yes-u {
  text-decoration: underline;
}
.u-hover {
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}
.pointer {
  cursor: pointer;
}
.upper {
  text-transform: uppercase;
}
.lower {
  text-transform: none;
}
.rounded {
  border-radius: $border-radius;
  overflow: hidden;
}
.uu {
  text-decoration: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 10%;
}

.font-bold {
  font-weight: bold;
}

.p-space {
  padding: $space !important;
}
.px-space {
  padding-left: $space !important;
  padding-right: $space !important;
}
.py-space {
  padding-top: $space !important;
  padding-bottom: $space !important;
}
.space-1 {
  height: #{$space * 1};
}
.space-2 {
  height: #{$space * 2};
}
.space-3 {
  height: #{$space * 3};
}
.space-4 {
  height: #{$space * 4};
}
.space-6 {
  height: #{$space * 6};
}
.space-8 {
  height: #{$space * 8};
}

.fades {
  transition: opacity 200ms;
  &.hide {
    opacity: 0;
  }
}

// -----------------------------------------------------------------------------
// Variables
// -----------------------------------------------------------------------------

:root {
  --sidebar-width: 330px;
}

// -----------------------------------------------------------------------------
// Tags
// -----------------------------------------------------------------------------

* {
  font-family: $font-sans;
  // text-rendering: auto;
  // text-rendering: optimizeSpeed;
  // text-rendering: optimizeLegibility;
  text-rendering: geometricPrecision; // OK on chrome
}

body {
  overflow-x: hidden;
  width: 100vw;
  background-color: $color-bg;
  background-position: top center;
  background-size: 100%;
  background-repeat: repeat-y;
  color: black;
  &.media-open {
    overflow: hidden;
  }
  &[data-bg-scale-mobile="no"] {
    @include media-breakpoint-down(md) {
      background-size: 950px auto;
    }
  }
}

main {
  // min-height: calc(100vh - #{$menu-h});
  // min-height: 100vh;
  // padding-top: $menu-h + $space;
  padding-top: $menu-h;
}

hr {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

a {
  text-decoration: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 10%;
  color: inherit;
  // &:hover { color: white; }
}

p {
  @include font_style_p;
  margin-bottom: 1em;
}
small {
  @include font-sans-s;
}
ul {
  list-style-type: none;
  position: relative;
  li {
    position: relative;
    padding: 0 0 0 22px;
    margin-bottom: 0.5em;
    p {
      margin: 0;
    }
    &::before {
      content: "â†’";
      position: absolute;
      left: 0;
      top: -2px;
    }
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
  margin-top: 1.8em;
  margin-bottom: 0.8em;
}
h1 {
  @include font_style_h1;
  text-transform: uppercase;
  color: $color-luca-2;
}
h2 {
  @include font_style_h2;
  color: $color-luca-2;
}
h3 {
  @include font_style_h3;
}
h4 {
  @include font_style_h4;
  text-transform: uppercase;
}
h5 {
  @include font_style_h5;
}
h6 {
  @include font_style_h6;
}

img {
  display: block;
  width: 100%;
  &.small-icon {
    width: 16px;
    height: 16px;
    display: inline-block;
  }
}
figure {
  display: block;
  width: 100%;
  figcaption p,
  figcaption {
    @include font-sans-s;
    // font-size: 12px;
    opacity: 0.5;
    margin-top: 8px;
  }
}

// -----------------------------------------------------------------------------
// REUSABLE PIECES
// -----------------------------------------------------------------------------

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 45px;
  border: 1px solid black;
  background-color: black;
  color: white;
  @include font_style_button;
  text-transform: uppercase;
  text-decoration: none;
  border-radius: $border-radius;
  padding: 0 25px;
  transition: all 200ms;
  &.secondary {
    border: 1px solid black;
    background-color: transparent;
    color: black;
  }
  &.size-s {
    height: 27px;
    @include font-sans-s;
    text-transform: none;
    padding: 0 0.5em;
  }
  &.size-m {
    height: 60px;
    @include font-sans-l;
    text-transform: none;
    padding: 0 1em;
  }
  &.size-l {
    height: 90px;
    @include font-sans-xl;
    text-transform: none;
    padding: 0 20px;
  }
  &:hover,
  &.active {
    color: white;
    background-color: $color-luca-2;
    border-color: $color-luca-2;
  }
  & + .button {
    margin-left: 5px;
  }
  &.dataviz {
    height: 36px;
    color: black;
    background-color: rgba(white, 0.3);
    border: none;
    // @include font-sans-l;
    padding: 0 9px;
    &:hover {
      background-color: rgba(white, 1);
      animation: none !important;
    }
    &.active {
      background-color: white;
    }
    &.square {
      width: 36px;
    }
    &.square-up-to-sm {
      @include media-breakpoint-down(sm) {
        width: 36px;
      }
    }
    &.square-up-to-md {
      @include media-breakpoint-down(md) {
        width: 36px;
      }
    }
    &.left {
      @include media-breakpoint-up(md) {
        width: 50%;
      }
      border-top-right-radius: 0;
      border-bottom-right-radius: 0;
    }
    &.right {
      @include media-breakpoint-up(md) {
        width: 50%;
      }
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      margin-left: 0 !important;
    }
    &.highlight {
      animation: blink 5s infinite;
    }
  }
}

.flying-button {
  $fbh: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  height: $fbh;
  border-radius: #{$fbh / 2};
  padding: 0 10px;
  text-decoration: none;
  box-shadow: 0px 2px 14px -6px rgba(0, 0, 0, 0.43);
}

@keyframes blink {
  0% {
    background-color: rgba(white, 0.3);
  }
  25% {
    background-color: rgba(white, 0.3);
  }
  50% {
    background-color: rgba(white, 1);
  }
  75% {
    background-color: rgba(white, 0.3);
  }
  100% {
    background-color: rgba(white, 0.3);
  }
}

$dotSize: 0.6em;
$dotSize-s: 0.35em;
.color-dot {
  display: inline-block;
  margin-right: 0.2em;
  width: $dotSize;
  height: $dotSize;
  border-radius: #{$dotSize / 2};
  position: relative;
  top: -0.05em;
  &.small {
    width: $dotSize-s;
    height: $dotSize-s;
    margin-right: 0.1em;
  }
}

// -----------------------------------------------------------------------------
// SPECIFIC
// -----------------------------------------------------------------------------

.sidebar {
  @include media-breakpoint-up(md) {
    z-index: 3;
    position: fixed;
    top: $menu-h;
    bottom: 0;
    left: 0;
    width: var(--sidebar-width);
    padding-bottom: $space;
    overflow-x: hidden;
    overflow-y: scroll;
  }

  background-color: white;
  padding-top: $dataviz-header-h;
  padding-left: $space;
  padding-right: $space;
  p,
  a,
  span {
    @include font-sans-s;
  }

  .sidebar-image {
    background-color: #eee;
    background-size: cover;
    background-position: center center;
    aspect-ratio: 16/9;
  }
  .sidebar-texts {
    padding: 20px;
  }

  .filter-link {
    display: block;
    margin: 3px 0;
    text-decoration: none;
    &.active {
      opacity: 0.3;
    }
    &:hover {
      text-decoration: underline;
    }
  }
}

.accordion-content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out;
  .filter-link:last-of-type {
    margin-bottom: 20px;
  }
}
.accordion-header {
  cursor: pointer;
}
.header-row {
  height: 40px;
  @include media-breakpoint-up(md) {
    height: 50px;
  }
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-left: -$space;
  padding-left: $space;
  margin-right: -$space;
  padding-right: $space;
  &:not(:first-of-type) {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }
  &.accordion-header {
    &::after {
      content: url("../images/icon-accordion.svg");
      font-size: 0;
      opacity: 0.7;
      transition: opacity 100ms;
    }
    &.active::after {
      transform: rotate(180deg);
    }
    &:hover {
      background-color: rgba($color-bg, 0.2);
      &::after {
        opacity: 1;
      }
    }
  }
}
.active-filters-list {
  padding: 0px 0 20px 0;
  // border-top: 1px solid rgba(0, 0, 0, 0.1);
  a {
    display: inline-flex;
    align-items: center;
    margin: 3px 3px 0 0;
    text-decoration: none;
    padding: 0 9px 1px 9px;
    border-radius: 5px;
    background-color: rgba(black, 0.9);
    color: white;
    height: 1.8em;
    span {
      color: grey;
      margin-left: 0.7em;
    }
    &:hover span {
      color: white;
    }
  }
}

.animated-blob {
  position: relative;
  width: 100%;
  height: 100%;
  aspect-ratio: 1/1;
  max-width: 400px;
  margin: auto;
  .outer-cont {
    width: 100%;
    // height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  .artifact-cover {
    $amount: 5px;
    z-index: 4;
    position: absolute;
    top: -#{$amount / 2};
    left: -#{$amount / 2};
    width: calc(100% + #{$amount});
    height: calc(100% + #{$amount});
    border: $amount solid $color-bg;
    // border: $amount solid yellow;
  }
  .svg-cont {
    aspect-ratio: 1/1;
    position: relative;
    font-size: 0;
    width: 100%;
    height: 100%;
    max-width: 100%;
    max-height: 100%;

    img.svg-container-bg {
      z-index: 2;
      aspect-ratio: 1/1;
      position: absolute;
      overflow: hidden;
      display: block;
      top: 50%;
      left: 50%;
      width: 100%;
      transform: translate(-50%, -50%);
      // transition: opacity ...ms; --- in snippet
    }
    svg {
      z-index: 3;
      position: relative;
      width: 100%;
      height: 100%;
      max-width: 100%;
      max-height: 100%;
      path {
        stroke-width: 2px;
        stroke: $color-bg;
        // stroke: red;
      }
    }
  }
}

#header-sidebar {
  z-index: 4;
  position: fixed;
  top: $menu-h;
  left: 0;
  width: 170px;
  @include media-breakpoint-up(md) {
    width: var(--sidebar-width);
  }
  height: $dataviz-header-h;
  background-color: $color-bg-filters;
  // background-color: rgba($color-bg-filters, 0.9);
  // backdrop-filter: blur(10px);
  display: flex;
  align-items: center;
  justify-content: stretch;
  padding: $space / 2;
}

#header-dataviz {
  z-index: 4;
  position: fixed;
  top: $menu-h;
  left: 170px;
  @include media-breakpoint-up(md) {
    left: var(--sidebar-width);
  }
  right: 0;
  height: $dataviz-header-h;
  background-color: $color-bg-filters;
  // background-color: rgba($color-bg-filters, 0.9);
  // backdrop-filter: blur(10px);
  // border-radius: #{$dataviz-header-h / 2};
  display: flex;
  align-items: center;
  justify-content: space-between;
  // border-left: 1px solid #f2f5f9;
  border-left: 1px solid rgba(white, 0.3);
  padding-left: $space/2;
  padding-right: $space/2;
  @include font-sans-s;

  &.groups {
    padding-left: $space;
    padding-right: $space;
    justify-content: flex-start;
  }
  &.dataviz-page {
    padding-left: $space;
  }
}

#header-search {
  z-index: 4;
  position: fixed;
  top: $menu-h;
  left: 0;
  right: 0;
  height: $dataviz-header-h;
  background-color: $color-bg-filters;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: $space;
  padding-right: $space/2;
  @include font-sans-s;
}

form {
  input {
    font-weight: 700;
    background-color: transparent;
    padding: 5px 0;
    // border-bottom: 1px solid rgba(black, 0.1);
    margin-right: 10px;
    border-radius: 0 !important;
  }
}

a.button-with-comment {
  text-decoration: none;
  width: 100%;
  display: block;
  border-radius: 5px;
  // background-color: rgba(0, 0, 0, 0.05);
  border: 1px solid rgba($color-bg, 0.3);
  margin-bottom: 5px;
  &:hover,
  &.active {
    background-color: rgba($color-bg, 1);
    border: 1px solid $color-bg;
  }
  span.header {
    display: flex;
    padding: 10px;
    align-items: center;
    justify-content: space-between;
    text-decoration: none;
    @include font_style_p_medium;
    &::after {
      content: "â†’";
    }
  }
  span.comment {
    display: none;
    padding: 10px;
    padding-top: 0px;
  }
  &.active span.comment {
    display: block;
  }
}

#dataviz-groups {
  min-height: 100vh;

  #viz {
    padding: $space;
    padding-top: #{$menu-h + $space};
    @include media-breakpoint-up(md) {
      padding-left: calc(var(--sidebar-width) + #{$space});
    }
    .group {
      margin: $space;
      header {
        // display: flex;
        align-items: flex-end;
        justify-content: space-between;
        border-bottom: 1px solid black;
      }
      .stats {
        padding-top: 9px;
        .stat {
          position: relative;
          display: flex;
          align-items: center;
          justify-content: space-between;
          min-height: 1.2em;
          .stat-label {
            position: relative;
            background-color: $color-bg;
            padding-right: 5px;
            @include font-sans-s;
          }
          .stat-number {
            position: relative;
            width: 22px;
            background-color: $color-bg;
            text-align: center;
            padding: 0 5px;
            @include font-sans-s;
            font-size: 8px;
            font-size: 8px !important;
            display: flex;
            align-items: center;
            justify-content: center;
            .text {
              z-index: 2;
            }
            .viz-circle {
              z-index: 1;
              position: absolute;
              width: 10px;
              height: 10px;
              border-radius: 50%;
              border: 1px solid rgba(0, 0, 0, 0.1);
              border: 0.5px solid $color-bg;
            }
          }

          &[data-count="0"] .stat-label {
            color: rgba(0, 0, 0, 0.2);
          }
          &[data-count="0"] .stat-number {
            color: rgba(0, 0, 0, 0.2);
            .viz-circle {
              display: none;
            }
          }

          &::before {
            content: "";
            display: block;
            width: 100%;
            height: 0px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
            position: absolute;
          }
        }
      }
    }
  }
}

#dataviz {
  min-height: 100vh;
}

#shapes {
  @include media-breakpoint-up(md) {
    padding-left: var(--sidebar-width);
    padding-top: $dataviz-header-h;
  }
  &.full-width {
    padding-top: $dataviz-header-h;
    @include media-breakpoint-up(md) {
      padding-left: 0;
      padding-top: $dataviz-header-h;
    }
  }

  .items {
    z-index: 4;
    // background-color: rgba(cyan, 0.1);
    display: flex;
    flex-flow: row wrap;
    align-items: stretch;
    justify-content: flex-start;

    &.small .item {
      width: 50%;
      @include media-breakpoint-up(sm) {
        width: 33.333%;
      }
      @include media-breakpoint-up(lg) {
        width: 16.666%;
      }
    }

    .item {
      display: inline-block;

      width: 50%;
      @include media-breakpoint-up(md) {
        width: 33.333%;
      }
      @include media-breakpoint-up(xl) {
        width: 25%;
      }
      @media (min-width: 1600px) {
        width: 20%;
      }
      @media (min-width: 2400px) {
        width: 16.666%;
      }

      border-right: 1px solid rgba(0, 0, 0, 0.05);
      border-bottom: 1px solid rgba(0, 0, 0, 0.05);

      .dots {
        position: relative;
        top: -17px;
        left: -10px;
      }

      a {
        padding: 20px;
        text-decoration: none;
        height: 100%;
        transition: background-color 200ms;
        &:hover {
          background-color: rgba(white, 0.3);
        }
      }
      .shape {
        margin-bottom: 10px;
        svg.pioneer-shape {
          width: 100%;
          max-width: 100%;
          height: auto;
          // background-color: yellow;
          transition: transform 200ms;
        }
        svg.placeholder {
          height: 150px;
          width: 100%;
          background: pink;
          border-radius: 50%;
        }
      }
      .name {
        text-align: center;
        @include font_s_strong;
      }
    }
  }
}

#pioneer-page-v2 {
  position: relative;

  .pioneer-shape-area {
    position: fixed;
    left: 50%;
    width: 50%;
    top: $menu-h + $dataviz-header-h;
    height: calc(100% - #{$menu-h} - #{$dataviz-header-h});
    padding: 80px;
    @include media-breakpoint-down(lg) {
      padding: 40px;
    }
    @include media-breakpoint-down(sm) {
      position: relative;
      left: 0;
      width: 100%;
      aspect-ratio: 1/1;
      max-height: 500px;
      padding: 30px;
      top: $menu-h;
      margin-bottom: $menu-h * 1.5;
    }

    svg.pioneer-shape {
      position: relative;
      width: 100%;
      height: 100%;
      // border: 1px solid rgba(green, 0.1);

      // v2 - in svg
      g.sector-label {
        rect {
          fill: black;
          fill: white;
        }
        text {
          fill: white;
          fill: black;
          font-size: 2.5px;
        }
        // transition: opacity 400ms;
        &.hide {
          opacity: 0;
        }
      }
    }
  }

  .pioneer-info {
    position: relative;
    background-color: white;
    top: 0;
    width: 50%;
    padding-top: $dataviz-header-h;
    .pioneer-texts {
      padding: $space;
    }
    .pioneer-photo {
      background-color: #eee;
      background-size: cover;
      background-position: center center;
      aspect-ratio: 16/9;
    }
    @include media-breakpoint-down(sm) {
      width: 100%;
      padding-top: 0;
    }
  }

  #header-sidebar {
    width: 50%;
  }
  #header-dataviz {
    width: 50%;
    left: 50%;
    @include media-breakpoint-down(sm) {
      border-left: none;
      justify-content: flex-end;
    }
  }
}

.related-pioneers {
  z-index: 5;
  position: relative;
}

