section#footer {
  z-index: 7;
  position: relative;
  // min-height: 600px;
  background-color: black;
  color: white;

  h5 {
    color: #686868;
  }
  .content,
  .content p {
    line-height: 1.6em;
  }
  a {
    // @extend .uu;
    text-decoration: none;
    &:hover {
      opacity: 0.85;
    }
  }
}

.no-max-w {
  max-width: none !important;
}

nav#menu-header {
  z-index: 6;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transition: background-color 400ms, padding 400ms;
  padding: 0;
  color: black;
  background-color: white;
  // &.top {  }
  .wrapper {
    height: $menu-h;
  }
  .left {
    @include font-sans-m;
    span.logo {
      display: inline-block;
      background-position: center left;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
  .items {
    a.item {
      text-decoration: none;
      margin-left: 15px;
      @include font_style_p_medium;
      // font-weight: 700;
      &.active,
      &:hover {
        // text-decoration: underline;
        color: $color-luca-2;
      }
    }
  }
  a#header-low-a {
    margin-right: 3px;
    &:hover {
      color: grey;
      text-decoration: none;
    }
  }
}
#menu-xs {
  z-index: 5;
  position: fixed;
  top: 0;
  right: -10%;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  background-color: white;
  padding: $menu-h 0;
  opacity: 0;
  pointer-events: none;
  transition: right 400ms, opacity 400ms;
  @at-root body.menu-open & {
    opacity: 1;
    right: 0;
    pointer-events: all;
  }
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: stretch;

  a.item {
    // color: white;
    text-decoration: none;
    text-align: center;
    display: block;
    @include font_style_h1;
    // text-transform: uppercase;
    &.active,
    &:hover {
      // text-decoration: underline;
      // color: inherit;
      color: $color-luca-2;
    }
    &.small {
      @include font-sans-m;
    }
  }
}

#legend-dataviz {
  background-color: $color-bg;
  z-index: 5;
  position: fixed;
  top: $menu-h;
  bottom: 0;
  right: 0;
  left: 0;
  overflow-y: scroll;
  display: flex;
  align-items: stretch;
  justify-content: stretch;
  opacity: 0;
  flex-flow: column nowrap;
  transition: opacity 200ms;
  pointer-events: none;
  &.active {
    opacity: 1;
    pointer-events: all;
  }
  .top-bar {
    // border: 1px solid red;
    background-color: $color-bg-filters;
    height: $dataviz-header-h;
    min-height: $dataviz-header-h;
  }
  .title {
    // border: 1px solid blue;
    display: flex;
    align-items: flex-end;
    flex-grow: 1;
    justify-content: center;
  }
  .content {
    display: flex;
    width: 100%;
    align-items: flex-start;
    justify-content: center;
    flex-grow: 1;
    // border: 1px solid blue;
  }
}

.show-only-when-portrtait {
  display: none;
  @media screen and (min-aspect-ratio: 3/4) {
    display: block;
  }
}
.show-only-when-landscape {
  display: none;
  @media screen and (max-aspect-ratio: 3/4) {
    display: block;
  }
}

#close-legend-button {
  z-index: 6;
  position: fixed;
  top: $menu-h + 10px;
  right: 10px;
}

// -----------------------------------------------------------------------------
// Blocks
// -----------------------------------------------------------------------------

.blocks {
  // --- Kirby standard blocks

  // h1 {
  //   text-align: center;
  // }

  .block {
    // --- colors of writer content

    .kt.writer {
      hr {
        margin: $space 0;
      }
    }

    // --- Block options :: Spacing

    &[data-paddingbottom="none"] {
      padding-bottom: 0px;
    }
    &[data-paddingbottom="one"] {
      padding-bottom: 25px;
    }
    &[data-paddingbottom="two"] {
      padding-bottom: 50px;
    }
    &[data-paddingbottom="three"] {
      padding-bottom: 75px;
    }
    &[data-paddingbottom="four"] {
      padding-bottom: 100px;
    }

    &[data-paddingtop="none"] {
      padding-top: 0px;
    }
    &[data-paddingtop="one"] {
      padding-top: 25px;
    }
    &[data-paddingtop="two"] {
      padding-top: 50px;
    }
    &[data-paddingtop="three"] {
      padding-top: 75px;
    }
    &[data-paddingtop="four"] {
      padding-top: 100px;
    }

    // --- Block options :: Background

    &[data-bgcolor="grey"] {
      background-color: $color-bg;
    }
    &[data-bgcolor="white"] {
      background-color: white;
    }
    &[data-bgcolor="black"] {
      background-color: black;
    }

    // --- Block options :: Borders

    &[data-bordertop="yes"] {
      border-top: 1px solid rgba(black, 0.1);
    }
    &[data-borderbottom="yes"] {
      border-bottom: 1px solid rgba(black, 0.1);
    }

    &[data-gridstyle="wide"] {
      width: 100%;
      padding: 0 $space;
      max-width: $grid-wide-max-width;
      margin-left: auto;
      margin-right: auto;
    }
    &[data-gridstyle="textWidth"] {
      padding-left: 60px;
      padding-right: 60px;
      @include media-breakpoint-down(sm) {
        padding-left: 60px;
        padding-right: 60px;
      }
      @include media-breakpoint-down(xs) {
        padding-left: 25px;
        padding-right: 25px;
      }
      width: 100%;
      max-width: $grid-max-width;
      margin-left: auto;
      margin-right: auto;
    }

    // -------------------------------------
    // Block types
    // -------------------------------------

    &.block-image {
      &[data-ratio="1/1"] img {
        aspect-ratio: 1/1;
      }
      &[data-ratio="16/9"] img {
        aspect-ratio: 16/9;
      }
      &[data-ratio="10/8"] img {
        aspect-ratio: 10/8;
      }
      &[data-ratio="21/9"] img {
        aspect-ratio: 21/9;
      }
      &[data-ratio="7/5"] img {
        aspect-ratio: 7/5;
      }
      &[data-ratio="4/3"] img {
        aspect-ratio: 4/3;
      }
      &[data-ratio="5/3"] img {
        aspect-ratio: 5/3;
      }
      &[data-ratio="3/2"] img {
        aspect-ratio: 3/2;
      }
      &[data-ratio="3/1"] img {
        aspect-ratio: 3/1;
      }
      &[data-crop] img {
        object-fit: cover;
      }
    }

    &.block-img_cta {
      position: relative;
      .wrapper {
        // border: 1px solid red;
        position: relative;
        .contents {
          z-index: 1;
          padding: $space;
          color: white;
          position: absolute;
          display: flex;
          align-items: flex-end;
          justify-content: flex-start;
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          @include media-breakpoint-up(lg) {
            padding: 0 15%;
            align-items: center;
          }
        }
      }
      &[data-style="wide"] {
        padding: $space;
        width: 100%;
        max-width: $grid-wide-max-width;
        margin-left: auto;
        margin-right: auto;
        // figure img {}
        .contents {
          padding: #{$space * 1.5};
          @include media-breakpoint-up(lg) {
            padding-left: 10px;
          }
        }
      }
      &[data-style="textWidth"] {
        padding: $space;
        padding-left: 60px;
        padding-right: 60px;
        @include media-breakpoint-down(sm) {
          padding-left: 60px;
          padding-right: 60px;
        }
        @include media-breakpoint-down(xs) {
          padding-left: 25px;
          padding-right: 25px;
        }
        width: 100%;
        max-width: $grid-max-width;
        margin-left: auto;
        margin-right: auto;
        // figure img {}
        .contents {
          padding: #{$space * 1.5};
          @include media-breakpoint-up(lg) {
            padding-left: 10px;
          }
        }
      }
    }

    // -------------------------------------

    &.block-accordion {
      hr {
        pointer-events: none;
      }
      .acc-item {
        .acc-item-header {
          display: flex;
          align-items: center;
          justify-content: space-between;
          cursor: pointer;
          .text {
            padding: 15px 0;
            h1,
            h2,
            h3,
            h4,
            h5,
            h6 {
              margin: 0;
            }
            flex-grow: 1;
            // p {
            //   padding: 20px 0;
            //   cursor: pointer;
            //   font-weight: 900;
            //   max-width: 74em;
            //   margin: 0;
            //   margin-bottom: -2px;
            // }
          }
          .arrow {
            font-size: 23px;
            // font-weight: 500 !important;
            transition: transform 200ms, opacity 200ms;
            margin: -10px;
            margin-right: 10px;
            transform: rotate(45deg);
            opacity: 0.5;
          }
        }
        .acc-item-content {
          margin-top: -5px;
          margin-bottom: 25px;
          p {
            max-width: 74em;
          }
        }
        &:hover {
          .arrow {
            opacity: 1;
          }
        }
        &.open {
          .arrow {
            opacity: 1;
            transform: rotate(90deg);
          }
        }
      }
    }
  }

  // Custom sections

  .image-grid {
    background-color: #d6dae3;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    @include media-breakpoint-down(sm) {
      grid-template-columns: repeat(auto-fill, minmax(160px, 1fr));
    }
    gap: 1px;
    padding-top: 1px;
  }

  .image-grid img {
    width: 100%;
    height: auto;
    display: block;
    // border: 1px solid grey;
  }
}

// ------------------------------------------------------------------------
// Layout
// ------------------------------------------------------------------------

.container-fluid {
  max-width: $grid-max-width;
  margin-left: auto;
  margin-right: auto;
  padding-left: 60px;
  padding-right: 60px;
  @include media-breakpoint-down(xs) {
    padding-left: 25px;
    padding-right: 25px;
  }
  &.no-max-width {
    max-width: none;
    // padding-left: $space !important;
    // padding-right: $space !important;
  }
}

// -----------------------------------------------------------------------------
// BOOTSTRAP BREAKPOINTS
// -----------------------------------------------------------------------------

// xs ---                 Portrait phones
// sm ---  576px and up   Small devices (landscape phones)
// md ---  768px and up   Medium devices (tablets)
// lg ---  992px and up   Large devices (desktops)
// xl --- 1200px and up   Extra large devices (large desktops)

// No media query necessary for xs breakpoint as it's effectively `@media (min-width: 0) { ... }`
// @include media-breakpoint-up(sm) { ... }
// @include media-breakpoint-only(md) { ... }
// @include media-breakpoint-between(md, xl) { ... }
// @include media-breakpoint-down(lg) { ... }
